<template>
  <div class="videoBox">
    <div class="video">
      <i class="el-icon-back" @click="back"></i>
      <video-player
        class="video-player"
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions"
      ></video-player>
    </div>
    <div class="videoList">
      <span class="title">{{ $route.query.title }}</span>
      <ol>
        <li
          v-for="(item, index) in videoList"
          :key="item.id"
          @click="toVideoPage(index)"
        >
          <div class="itembox">
            <span>{{ videoName[index] }}</span>
            <i class="el-icon-download" @click="download"></i>
          </div>
        </li>
      </ol>
    </div>
  </div>
</template>

<script>
import "video.js/dist/video-js.css";
export default {
  mounted() {
    console.log(this.videoList);
    let myPlayer = this.$refs.videoPlayer.player;
    myPlayer.src(this.videoList[this.$route.query.index]);
  },
  data() {
    return {
      videoName: this.$route.query.videoName,
      videoList: JSON.parse(this.$route.query.videoList),
      playerOptions: {
        height: 400,
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        autoplay: false, // true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "video/mp4",
            src: "",
          },
        ],
        // width: document.documentElement.clientWidth, //
        notSupportedMessage: "", // Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true, // 
        },
      },
    };
  },
  methods: {
    back() {
      this.$router.back(-1);
    },
    toVideoPage(index) {
      let myPlayer = this.$refs.videoPlayer.player;
      myPlayer.src(this.videoList[index]);
    },
    download() {},
  },
};
</script>

<style scoped>
.video {
  display: flex;
  flex-direction: column;
}
.el-icon-back {
  color: white;
  position: absolute;
  font-size: 3rem;
  left: 5%;
  top: 2%;
  z-index: 5;
}
.video-player {
  width: 100vw;
}
.videoList .title {
  font-size: 2rem;
}
.videoList {
  padding: 1rem 2rem 1rem 2rem;
  box-sizing: border-box;
}
.videoList ol {
  padding: 1rem 2rem;
  margin-left: 1.5rem;
}
.videoList ol li {
  margin-left: -0.5rem;
  padding: 0.5rem 1.5rem;
  font-size: 1.5rem;
}
.itembox {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
</style>